@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');

.article {
  font-family: 'Roboto', 'sans-serif';
  border-radius: 10px;
  background-image: linear-gradient(180deg, white, silver);
  padding: 10px;
  margin: 5px;
  width: 50%;
  box-shadow: 0px 20px 30px rgba(125, 123, 123, 0.924);
  color: rgb(122, 119, 119);

}

h1 {
  font-family: 'Roboto';
  color: rgb(8, 104, 194);
  font-size: 18px;
  font-weight: bold;
  padding: 10px;
}