@import url('https://fonts.googleapis.com/css2?family=Montserrat&family=Roboto&display=swap');

.welcomeMsg {
    position: relative;
    width: 100%;
    height: 50vmax;
    border-radius: 50px;
    /* border: 1px solid black; */

}

.backgroundBlur {
    position: absolute;
    width: inherit;
    height: inherit;
    /* background-image: linear-gradient(180deg, rgba(0, 100, 0, 0.332), rgba(255, 255, 0, 0.473), rgba(0, 100, 0, 0.4)); */
    filter: blur(20px);
    -webkit-filter: blur(20px);
    z-index: 0;
}

.headerText {
    color: white;
    font-size: 48px;
    position: relative;
    z-index: 1;
    font-family: "Montserrat";
    text-align: center;
    font-weight: bold;

}

.headerMsg {
    position: relative;
    font-family: "Montserrat";
    text-align: center;
    color: white;
    width: 50%;
    margin: auto;
    align-content: center;

}

.ctaBtn {
    position: relative;
    justify-content: center;
    text-align: center;

}

.ctaBtn button {
    padding: 10px 20px;
    border: none;
    border-radius: 50px;
}