.navbar {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 2px;
    /* background-color: rgb(13, 102, 32); */
    /* background-blend-mode: color-burn; */
}

.logo {
    width: 20%;
    height: 20%;
    border-radius: 30px;
}

.navbar ul {
    display: flex;
    list-style: none;
    justify-content: space-evenly;
    align-self: center;

}



.navbar ul li a {
    text-decoration: none;
    color: rgb(252, 252, 255);
    padding: 8px 20px;
    border-radius: 50px;
    margin-left: 0;
    font-weight: 400;
}

@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');

.navbar ul li a:hover {
    background-color: rgb(231, 167, 17);
    color: white;
}

.cta button {

    border: none;
    width: fit-content;
    background-image: linear-gradient(silver, white);
    padding: 10px 20px;
    border-radius: 50px;
    color: darkblue;
    box-shadow: 10px;


}

.cta button a {
    font-size: 18px;
}

.cta button:hover {
    background-image: linear-gradient(30deg, silver, white);
    scale: 0.9;
}